.advantages-container {
  width: 70%;
  margin: 2rem auto;
  min-height: 120vh;
  display: flex;
  gap: 50px;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.advantages-container h1 {
  text-align: center;
  font-weight: 600;
  color: #f2a444;
  font-size: 40px;
  line-height: 1.1111111111;
  letter-spacing: 1px;
  filter: drop-shadow(2px 2px #333333);
  margin: 0 auto;
}
.advantages-container h2 {
  width: 80%;
  font-weight: 400;
  font-size: 20px;
  letter-spacing: 1px;
  text-align: center;
  line-height: 1.6;
}

.advantage-container {
  margin: 0 auto;
  display: grid;
  gap: 100px;
  grid-template-columns: 1fr 1fr 1fr;
}
.advantage-container h2 {
  width: 50%;
  font-size: 30px;
  text-align: left;
}
.advantage-container p {
  font-size: 18px;
  line-height: 2;
  text-align: justify;
}
.advantage-container .advantage {
  padding: 1rem;
  display: grid;
  grid-template-rows: 1fr 2fr;
  border-radius: 30px;
}
.advantage-container .advantage:nth-of-type(1) {
  background-color: #e7cfe6;
}
.advantage-container .advantage:nth-of-type(2) {
  background-color: #fcdeb9;
}
.advantage-container .advantage:nth-of-type(3) {
  background-color: #d7dbb6;
}