/* REST vars */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-size: 10px;
  scroll-behavior: smooth;
  overflow-x: hidden;
  background-color: #fff5ec;
  color: #333333;
  font-family: "Montserrat", sans-serif;
  font-weight: 300;
}

.basic-btn {
  padding: 1rem 4rem 1rem 3rem;
  text-transform: uppercase;
  font-family: inherit;
  font-size: 25px;
  font-weight: 300;
  color: #fefcfd;
  letter-spacing: 3px;
  border: none;
  border-radius: 30px;
  background-color: #f2a444;
  filter: drop-shadow(6px 6px #fcdeb9);
  overflow: hidden;
  transition: 0.5s;
  cursor: pointer;
}
.basic-btn:hover {
  transform: scale(0.9);
}
.basic-btn .icon-btn {
  position: absolute;
  top: 50%;
  right: 8%;
  transform: translateY(-50%);
  font-size: 35px;
  transition: 2s;
}
.basic-btn:hover .icon-btn {
  top: -100%;
  transform: scale(0.4);
}
.basic-btn:hover {
  transform: scale(0.9);
}

#big {
  width: 100vw;
  height: 100vh;
  position: absolute;
  top: -15%;
  right: -35%;
  z-index: -1;
  transform: rotate(50deg);
  border-radius: 30px;
  background-color: #e7cfe6;
}

#medium {
  width: 200vw;
  height: 170vh;
  position: absolute;
  bottom: -148%;
  left: -30%;
  z-index: -2;
  transform: rotate(170deg);
  background-color: #333333;
}

#small-1 {
  position: absolute;
  bottom: 55%;
  left: 48%;
  width: 6.6666666667vw;
  height: 6.6666666667vw;
  background-color: #9ca653;
  filter: drop-shadow(10px 10px #d7dbb6);
  transform: rotate(20deg);
  border-radius: 30px;
  animation: pulsatingSquare1 3s infinite;
}

@keyframes pulsatingSquare1 {
  0% {
    transform: rotate(20deg) scale(1);
  }
  50% {
    transform: rotate(20deg) scale(0.95);
  }
  100% {
    transform: rotate(20deg) scale(1);
  }
}
#small-2 {
  position: absolute;
  bottom: 10%;
  left: 45%;
  width: 8.3333333333vw;
  height: 8.3333333333vw;
  background-color: #bf88bc;
  filter: drop-shadow(15px 15px #e7cfe6);
  border-radius: 30px;
  animation: pulsatingSquare2 6s infinite;
}

@keyframes pulsatingSquare2 {
  0% {
    transform: rotate(-5deg) scale(1);
  }
  50% {
    transform: rotate(-25deg) scale(0.85);
  }
  100% {
    transform: rotate(-5deg) scale(1);
  }
}
#small-3 {
  position: absolute;
  bottom: 65%;
  left: 15%;
  width: 4.1666666667vw;
  height: 4.1666666667vw;
  background-color: #f2a444;
  filter: drop-shadow(5px 5px #fcdeb9);
  border-radius: 30px;
  animation: pulsatingSquare3 3s infinite;
}

@keyframes pulsatingSquare3 {
  0% {
    transform: rotate(-35deg) scale(1);
  }
  50% {
    transform: rotate(-65deg) scale(0.9);
  }
  100% {
    transform: rotate(-35deg) scale(1);
  }
}