/* REST vars */
.menu {
  height: 10vh;
  display: flex;
  gap: 20px;
  margin-left: 10rem;
  padding-left: 2rem;
  margin-top: 4rem;
}

.menu-element {
  padding: 0 1rem;
  color: #f2a444;
  filter: drop-shadow(2px 2px #333333);
  font-size: 30px;
  font-weight: 600;
  letter-spacing: 1px;
  text-transform: uppercase;
  border: 1px solid transparent;
  text-align: center;
  transition: 0.5s;
  cursor: pointer;
}
.menu-element:hover {
  color: #f2a444;
  transform: scale(0.9);
}