/* REST vars */
.registration-container {
  position: relative;
  min-height: 50vh;
  display: grid;
  gap: 100px;
  grid-template-rows: 0.05fr 0.95fr;
  color: #fefcfd;
}
.registration-container h1 {
  width: 50%;
  text-align: center;
  font-weight: 600;
  font-size: 40px;
  line-height: 1.3333333333;
  letter-spacing: 1px;
  filter: drop-shadow(2px 2px #bf88bc);
  margin: 0 auto;
}

.steps-container {
  width: 70%;
  margin: 0 auto;
  display: grid;
  gap: 10px;
  grid-template-columns: 3fr 3fr;
  place-content: center;
}
.steps-container .step {
  position: relative;
  width: 70%;
  justify-self: center;
}
.steps-container .step h2 {
  font-size: 30px;
  font-weight: 600;
  line-height: 1.5;
  letter-spacing: 1px;
  text-transform: uppercase;
  filter: drop-shadow(1px 1px #bf88bc);
  margin: 1rem 0;
}
.steps-container .step p {
  font-size: 20px;
  letter-spacing: 1px;
  line-height: 1.5;
  text-align: justify;
  font-weight: 400;
}

.arrow1 {
  position: absolute;
  top: 25%;
  left: 50%;
  transform: translateX(-50%);
  font-size: 100px;
}

.arrow2 {
  position: absolute;
  top: 45%;
  left: 50%;
  transform: translateX(-50%);
  font-size: 100px;
}

.arrow3 {
  position: absolute;
  top: 30%;
  left: 50%;
  transform: translateX(-50%);
  font-size: 100px;
}

.registration-icon {
  font-size: 150px;
  font-weight: 100;
  animation: pulsating 3s infinite;
}